import React from 'react';
import {Helmet} from "react-helmet";
import Dropzone from 'react-dropzone'
import ReCAPTCHA from "react-google-recaptcha";
import InputMask from 'react-input-mask';
import HeaderQuestionnaire from "../components/header_questionnaire";
import ApiProvider from "../providers/api_provider";

const validateEmail = email => {
    if (email === '') return true;
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

class Questionnaire extends React.Component {
    state = {
        vacancies: [],
        formDisable: true,
        id: '',
        fio: '',
        birthday: '',
        gender: '',
        phone: '',
        email: '',
        text: '',
        filename: '',
        captchaValue: '',
        agree: '',
    }

    checkFormFilled = () => {
        let disableFormSend = false;
        const state = this.state;

        console.log(state);
        if (state.id.length === 0) disableFormSend = true;
        if (state.fio.length === 0) disableFormSend = true;
        if (state.birthday.length === 0) disableFormSend = true;
        if (state.phone.length === 0) disableFormSend = true;
        if (state.captchaValue.length === 0) disableFormSend = true;
        if (state.agree.length === 0) disableFormSend = true;

        if (document.querySelector('.questionnaire__page-form__input-wrapper.error') !== null) {
            disableFormSend = true;
        }

        if (state.formDisable !== disableFormSend) {
            this.setState({
                formDisable: disableFormSend,
            })
        }
    }

    sendForm = async () => {
        const state = this.state;
        if (state.formDisable) return;
        let formData = new FormData();
        formData.append('id', state.id);
        formData.append('fio', state.fio);
        formData.append('birthday', state.birthday);
        formData.append('gender', state.gender);
        formData.append('phone', state.phone);
        formData.append('email', state.email);
        formData.append('text', state.text);

        if (state.filename.length > 0) {
            let files = document.querySelector('input[name="vacancy_file"]').files;
            if (files.length > 0) {
                for (let i = 0; i < files.length; i++) {
                    formData.append("files[]", files[i])
                }
            }
        }
        formData.append('method', 'questionnaire');

        await ApiProvider.sendForm(formData);
        document.querySelector('.questionnaire__page-form').style.display = 'none';
        document.querySelector('.questionnaire__page-result').style.display = 'block';
    }

    onChangeCaptcha = value => {
        this.setState({captchaValue: value});
        this.checkFormFilled();
    }

    loadData = async () => {
        const vacancies = await ApiProvider.getVacancies();
        this.setState({
            vacancies: vacancies,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    onBlurField = e => {
        const fieldName = e.target.getAttribute('name').replace('vacancy_', '');
        //TODO проверка - корретные ли данные

        if (fieldName === 'id' || fieldName === 'fio') {
            // проверка выбрана ли вакансия
            if (e.target.value.length === 0) {
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('complete');
            } else {
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('complete');
            }
        }

        if (fieldName === 'phone' || fieldName === 'birthday') {
            // проверка выбрана ли вакансия
            if (e.target.value.length === 0 || e.target.value.indexOf('_') !== -1) {
                const errorTextElement = document.getElementById(e.target.getAttribute('id')).nextElementSibling;
                if (e.target.value.length === 0) {
                    errorTextElement.innerHTML = 'поле не заполнено'
                } else {
                    errorTextElement.innerHTML = 'поле не заполнено до конца'
                }
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('complete');
            } else {
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('complete');
            }
        }

        if (fieldName === 'email') {
            if (validateEmail(e.target.value)) {
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('complete');
            } else {
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('error');
                document.getElementById(e.target.getAttribute('id')).parentElement.classList.add('complete');
            }
        }

        this.setState({
            [fieldName]: e.target.value,
        });
        this.checkFormFilled();
    }

    onFocusField = e => {
        document.getElementById(e.target.getAttribute('id')).parentElement.classList.remove('error');
    }

    render() {
        return (
            <React.Fragment>
                <Helmet>
                    <title>Работа твоей мечты</title>
                </Helmet>
                <HeaderQuestionnaire/>
                <div className="questionnaire__page">
                    <div className="wrapper">
                        <div className="questionnaire__page-form">
                            <div className="questionnaire__page-title h1">Работа твоей мечты</div>
                            <div className="questionnaire__page-content">
                                <div className="questionnaire__page-content__left">
                                    <div className="questionnaire__page-form__line">
                                        <div className="questionnaire__page-form__input-wrapper full-width">
                                            <label htmlFor="vacancy_id"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Вакансия *</span>
                                            </label>
                                            <select className="questionnaire__page-form__select" id="vacancy_id"
                                                    name="vacancy_id" onChange={this.onBlurField.bind(this)} onFocus={this.onFocusField.bind(this)}>
                                                {this.vacancyList()}
                                            </select>
                                            <div className="questionnaire__page-form__input-error caption">поле не
                                                заполнено
                                            </div>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__line">
                                        <div className="questionnaire__page-form__input-wrapper full-width">
                                            <label htmlFor="vacancy_fio"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>ФИО*</span>
                                            </label>
                                            <input type="text" id="vacancy_fio" name="vacancy_fio"
                                                   className="questionnaire__page-form__input" onBlur={this.onBlurField.bind(this)} onFocus={this.onFocusField.bind(this)}/>
                                            <div className="questionnaire__page-form__input-error caption">поле не
                                                заполнено
                                            </div>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__line">
                                        <div className="questionnaire__page-form__input-wrapper">
                                            <label htmlFor="vacancy_birthday"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Дата рождения *</span>
                                            </label>
                                            <InputMask className="questionnaire__page-form__input" id="vacancy_birthday"
                                                       name="vacancy_birthday" mask="99.99.9999"
                                                       placeholder="28.07.2002" onBlur={this.onBlurField.bind(this)} onFocus={this.onFocusField.bind(this)}/>
                                            <div className="questionnaire__page-form__input-error caption">поле не
                                                заполнено
                                            </div>
                                        </div>
                                        <div className="questionnaire__page-form__input-wrapper">
                                            <label htmlFor="vacancy_birthday"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Пол</span>
                                            </label>
                                            <div className="questionnaire__page-form__input__radio-group">
                                                <div className="questionnaire__page-form__input__radio">
                                                    <input type="radio" name="vacancy_gender" id="vacancy_gender_male"
                                                           value="мужской" onChange={this.onBlurField.bind(this)}/>
                                                    <label htmlFor="vacancy_gender_male"
                                                           className="questionnaire__page-form__input__radio-circle"/>
                                                    <label htmlFor="vacancy_gender_male"
                                                           className="questionnaire__page-form__input__radio-text caption">мужской</label>
                                                </div>
                                                <div className="questionnaire__page-form__input__radio">
                                                    <input type="radio" name="vacancy_gender" id="vacancy_gender_female"
                                                           value="женский" onChange={this.onBlurField.bind(this)}/>
                                                    <label htmlFor="vacancy_gender_female"
                                                           className="questionnaire__page-form__input__radio-circle"/>
                                                    <label htmlFor="vacancy_gender_female"
                                                           className="questionnaire__page-form__input__radio-text caption">женский</label>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__line">
                                        <div className="questionnaire__page-form__input-wrapper">
                                            <label htmlFor="vacancy_phone"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Контактый телефон *</span>
                                            </label>
                                            <InputMask className="questionnaire__page-form__input" id="vacancy_phone"
                                                       name="vacancy_phone" mask="+7 (999) 999 99 99"
                                                       placeholder="+7 (" onBlur={this.onBlurField.bind(this)} onFocus={this.onFocusField.bind(this)}/>
                                            <div className="questionnaire__page-form__input-error caption">поле не
                                                заполнено
                                            </div>
                                        </div>
                                        <div className="questionnaire__page-form__input-wrapper">
                                            <label htmlFor="vacancy_email"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Электронная почта</span>
                                            </label>
                                            <input type="text" id="vacancy_email" name="vacancy_email"
                                                   className="questionnaire__page-form__input" onBlur={this.onBlurField.bind(this)} onFocus={this.onFocusField.bind(this)}/>
                                            <div className="questionnaire__page-form__input-error caption">поле заполнено не корректно
                                            </div>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__line">
                                        <div className="questionnaire__page-form__input-wrapper full-width">
                                            <label htmlFor="vacancy_text"
                                                   className="questionnaire__page-form__input-title caption">
                                                <span>Резюме</span>
                                            </label>
                                            <textarea id="vacancy_text" name="vacancy_text"
                                                      className="questionnaire__page-form__textarea" onBlur={this.onBlurField.bind(this)}/>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__drop-input">
                                        <Dropzone multiple={false} onDrop={acceptedFiles => {
                                            if (acceptedFiles.length > 0) {
                                                this.setState({
                                                    filename: acceptedFiles[0].name
                                                })
                                            }
                                        }}>
                                            {({getRootProps, getInputProps}) => (
                                                <section className="questionnaire__page-form__drop-input__zone">
                                                    <div {...getRootProps()}>
                                                        <input name="vacancy_file" {...getInputProps()} />
                                                        <img src="/_images/attach.svg"
                                                             className="questionnaire__page-form__drop-input__icon"
                                                             alt=""/>
                                                        <div
                                                            className={`questionnaire__page-form__drop-input__label caption${this.state.filename.length > 0 ? ' filled' : ''}`}>
                                                            {this.state.filename.length > 0 ? this.state.filename : "выберете или перетащите файл"}
                                                        </div>
                                                    </div>
                                                </section>
                                            )}
                                        </Dropzone>
                                    </div>
                                    <div className="questionnaire__page-form__captcha-wrapper">
                                        <div className="questionnaire__page-form__captcha-title caption">Капча</div>
                                        <div className="questionnaire__page-form__captcha">
                                            <div className="questionnaire__page-form__recaptcha">
                                                <ReCAPTCHA
                                                    sitekey="6Le2SjwcAAAAAOSCDUwQG-QsHqslrOwHAeGlhwuQ"
                                                    onChange={this.onChangeCaptcha.bind(this)}
                                                    size="normal"
                                                />
                                            </div>
                                            <div className="questionnaire__page-form__captcha-description caption">
                                                * поля для обязательного заполнения
                                            </div>
                                        </div>
                                    </div>
                                    <div className="questionnaire__page-form__agree-wrapper">
                                        <input type="checkbox" value="Y" name="vacancy_agree" id="vacancy_agree" onChange={this.onBlurField.bind(this)}/>
                                        <label htmlFor="vacancy_agree"
                                               className="questionnaire__page-form__agree-label"/>
                                        <label htmlFor="vacancy_agree"
                                               className="questionnaire__page-form__agree-text caption">
                                            я подтверждаю согласие на обработку персональных <br/>данных и принимаю
                                            условия рассмотрения обращений *
                                        </label>
                                    </div>
                                    <div className="questionnaire__page-form__button-wrapper">
                                        <button
                                            className={`questionnaire__page-form__button ${this.state.formDisable ? ' disabled' : ''}`}
                                            onClick={() => this.sendForm()}
                                        >
                                            отправить
                                        </button>
                                    </div>
                                </div>
                                <div className="questionnaire__page-content__right">
                                    <div className="questionnaire__page-form__title h2">
                                        Наша суперцель
                                    </div>
                                    <div className="questionnaire__page-form__description">
                                        — стать любимым магазином для каждой российской семьи.<br/><br/>
                                        Сотни тысяч наших сотрудников ежедневно работают над её достижением.<br/><br/>
                                        Мы уверены, что в ближайшие годы достигнем этого и будет здорово, <br/>
                                        если вместе с тобой.
                                    </div>
                                    <a href="tel:+7 (926) 433-14-16" className="questionnaire__page-form__phone h3">
                                        +7 (926) 433-14-16
                                    </a>
                                </div>
                            </div>
                        </div>
                        <div className="questionnaire__page-result">
                            <div className="questionnaire__page-title h1">Ждем тебя!</div>
                            <div className="questionnaire__page-content">
                                <div className="questionnaire__page-content__left">
                                    <div className="questionnaire__page-content__result-text">
                                        В 2020 году самыми востребованными умениями и качествами на рынке труда станут:
                                    </div>
                                    <div className="questionnaire__page-content__result-quote caption">
                                        Умение ставить цели, планировать свое время, инициативность, настойчивость,
                                        высокая мотивация, умение эффективно общаться, любознательность.
                                    </div>
                                    <div className="questionnaire__page-content__result-text">
                                        А профессиональным навыкам можно научить любого человека.
                                    </div>
                                </div>
                                <div className="questionnaire__page-content__right">
                                    <div className="questionnaire__page-form__title h2">
                                        Остались вопросы?
                                    </div>
                                    <a href="tel:+7 (926) 433-14-16" className="questionnaire__page-form__phone h3">
                                        +7 (926) 433-14-16
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }

    vacancyList = () => {
        const options = [];
        for (const vacancy of this.state.vacancies) {
            options.push(<option value={vacancy.id} selected={this.props.match.params.id === vacancy.id ? true : false}>
                {vacancy.name}
            </option>)
        }

        return (
            <React.Fragment>
                <option value="">- выберите вакансию -</option>
                {options}
            </React.Fragment>
        )
    }
}

export default Questionnaire;